import Bmob from "hydrogen-js-sdk";
import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import loading from "@/components/Loading/index.vue"

let that;
let uid;
export default {
	components: {
		faIcon,
		loading
	},
	data() {
		return {
			staff: "",
			Goods: null,
			reserve_num: 0,
			reserve_money: 0,
		}
	},

	onLoad() {
		that = this;
		uid = uni.getStorageSync("uid");
		that.staff = uni.getStorageSync("staff")
	},
	methods: {

		show_options() {
			uni.showActionSheet({
				itemList: ["编辑", "删除"],
				success: function(res) {
					if (res.tapIndex == 0) {
						that.edit(that.staff)
					} else if (res.tapIndex == 1) {
						that.delete_this(that.staff.objectId)
					}
				},
				fail: function(res) {
					console.log(res.errMsg);
				}
			});
		},

		//编辑操作
		edit(item) {
			console.log(item)
			uni.setStorageSync("staff", item);
			uni.navigateTo({
				url: "../add/add"
			})
		},

		//删除操作
		delete_this(id) {
			uni.showModal({
				title: '提示',
				content: '是否删除此员工',
				success: function(res) {
					if (res.confirm) {
						console.log(id);
						that.delete_data(id)
					}
				}
			});
		},

		//删除数据
		delete_data(id) {
			console.log(id)
			const query = Bmob.Query("_User");
			query.destroy(id).then(res => {
				const query = Bmob.Query("_User");
				query.get(id).then(res => {
					if (res.userId) {
						const query = Bmob.Query("_User");
						query.destroy(res.userId).then(res => {
							uni.showToast({
								title: "删除成功",
								icon: "none"
							})
							uni.navigateBack({
								delta:1
							})
						})
					}
					console.log(res)
				}).catch(err => {
					console.log(err)
				})
				console.log(res)
				uni.showToast({
					title: "删除成功",
					icon: "none"
				})
				uni.navigateBack({
					delta:1
				})
			}).catch(err => {
				console.log(err)
			})
		},

		goto_edit() {
			uni.navigateTo({
				url: "../add/add"
			})
		},

		goto_detail(good) {
			uni.setStorageSync("now_product", good);
			uni.navigateTo({
				url: "/pages/manage/good_det/good_det"
			})
		},
	}
}
